.application {
  min-height: 200px;
  background-color: #f9f9f9;
  padding: 38px 67px 63px 67px;

  .main_width {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    h2 {
      color: #575757;
      font-size: 2.2rem;
      font-weight: normal;
      width: 322px;
      padding: 5px 0;
      border-bottom: 8px solid #24C7AA;
    }

    header {
      background-color: transparent;
    }

    header.align_right {
      text-align: right;
    }

    .inline {
      box-sizing: initial;

      display: inline-block;
      width: 322px;
      padding: 0;
      vertical-align: top;
    }

    .form {
      margin-top: 50px;
    }
    .drop_down_list {
      box-sizing: initial;

      border: 1px solid #8de0d2;
      width: 280px;
      height: 38px;
      background-color: white;
      margin-top: 22px;
      padding: 8px 12px;
      position: relative;
    }

    .list_label {
      font-size: 30px;
      color: #e2e2e2;
      font-family: 'ralewaylight', Arial, Tahoma, sans-serif;
    }

    .checkbox {
      position: relative;
      padding: 30px 30px 30px 0;
    }
    .checkbox input {
      display: block;
      opacity: 0;
      height: 48px;
      position: absolute;
      width: 48px;
      cursor: pointer;
    }

    .checkbox label {
      display: block;
      padding-left: 65px;
      position: relative;
      font-size: 28px;
      color: #cecece;
      line-height: 120%;
      padding-top: 10px;
      cursor: pointer;
    }

    .checkbox label::before, .checkbox label::after {
      border: 1px solid #00C8A9;
      content: "";
      display: block;
      left: 0;
      height: 48px;
      position: absolute;
      width: 48px;
      top: 0;
    }
    .checkbox label::after {
      background: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20xmlns%3Axlink%3D%22http%3A//www.w3.org/1999/xlink%22%20preserveAspectRatio%3D%22xMidYMid%22%20width%3D%2233.875%22%20height%3D%2233%22%20viewBox%3D%220%200%2033.875%2033%22%3E%0A%20%20%3Cpath%20d%3D%22M9.470%2C15.209%20L14.061%2C24.104%20C14.061%2C24.104%2021.522%2C6.026%2033.287%2C-0.000%20C33.000%2C4.304%2031.852%2C8.035%2033.861%2C12.626%20C28.696%2C13.774%2018.078%2C26.687%2014.635%2C33.000%20C9.756%2C26.974%204.017%2C22.382%20-0.000%2C20.948%20L9.470%2C15.209%20Z%22%20fill%3D%22%2327c7ab%22/%3E%0A%3C/svg%3E%0A") no-repeat center;
      border: 0;
      display: none;
    }

    .checkbox input:checked + label::after {
      display: block;
    }

    //.active {
    //  background: url('../images/form_ok.jpg');
    //  width: 48px;
    //  height: 40px;
    //
    //}

    .inline_text {
      display: inline-block;
      width: 228px;
      vertical-align: top;
      margin-top: 30px;
      margin-left: 15px;
      color: #aeaeae;
    }
    .image_part img {
      margin-top: 110px;
      margin-left: 38px;
    }

    .green_bell {
      background-color: #00C8A9;
      width: 200px;
      height: 6px;
      display: inline-block;
      margin: 5px 0 5px 0;
    }

    .summary_panel {
      box-sizing: initial;

      background-color: white;
      width: 100%;
      height: 195px;
      padding: 10px;
      margin-top: 50px;

    }

    .green_button {
      background-color: #00C8A9;
      color: white;
      text-align: center;
      text-transform: uppercase;
      width: 200px;
      padding: 10px 30px;
      font-size: 24px;
      display: inline-block;
      font-family: 'ralewaymedium', Arial, Tahoma, sans-serif;
      margin-top: 20px;
      margin-left: 29px;
      border-radius: 8px;
      text-decoration: none;
    }
    .panel_left {
      display: inline-block;
      width: 185px;
      vertical-align: top;
      padding-bottom: 10px;
      border-bottom: 1px solid #e7e7e7;
      height: 135px;
    }

    .panel_right {
      display: inline-block;
      width: 100px;
      border-left: 1px solid #e7e7e7;
      text-align: right;
      vertical-align: top;
      padding-bottom: 10px;
      border-bottom: 1px solid #e7e7e7;
      height: 135px;
    }

    .panel_left h4, .panel_right h4 {
      color: #24baa0;
      font-size: 23px;
    }

    .panel_left span {
      margin-top: 10px;
      font-size: 22px;
      color: #5b5a5a;
      display: block;
    }

    .panel_right span {
      display: block;
      font-size: 22px;
      color: #24baa0;
      margin-top: 10px;
    }
    .sum_label {
      display: inline-block;
      width: 185px;
      margin-top: 10px;
      font-size: 22px;
      color: #5b5a5a;
    }

    .sum {
      display: inline-block;
      width: 100px;
      text-align: right;
      margin-top: 10px;
      font-size: 22px;
      color: #24baa0;
    }

    .list_arrow {
      width: 0;
      height: 0;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-top: 15px solid #24baa0;
      display: block;
      position: absolute;
      right: 14px;
      top: 20px;
      cursor: pointer;
    }

    .list_panel {
      font-size: 1.6rem;
      background-color: white;
      border: 1px solid #ccc;
      width: 288px;
      padding-left: 15px;
      padding-bottom: 10px;
      list-style: none;
      margin: 0;
      left: 0px;
      z-index: 2;
      position: absolute;
      top: 54px;
      display: none;
    }

    .visible {
      display: block;
    }

    .list_panel li {
      margin-top: 10px;
      cursor: pointer;
      color: #585858;
    }

    .list_panel li:hover {
      text-decoration: underline;
    }

    .title {

      height: 30px;
    }
  }
}



